.c-recommended-products-carousel {
  display: none;
  max-width: $s-page-wrap--xlarge;
  margin: 0 auto;

  &.is-visible {
    display: block;

    .c-recommended-products-carousel__outer {
      min-height: 34.8125rem;
    }
  }


  &__product-thumbnail {
    padding: 0 5rem;
  }

  &__outer {
    position: relative;
  }

  &__inner {
    padding: 2.8125rem 0 4.75rem;
    text-align: center;
    overflow: hidden;
    max-width: 100%;
  }

  &__carousel {
    display: flex;
    overflow: hidden;
  }

  &__nav {
    @include t-absolute-center-y();
    top: 42.5%;
    left: 0;
    z-index: 2;
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding: 0 $s-gutter--edge-mobile;
    pointer-events: none;
  }

  &__nav-arrow {
    padding: 1rem;
    display: block;
    border-radius: 50%;
    background-color: rgba($s-color--background, 0.7);
    cursor: pointer;
    pointer-events: all;
  }

  &__arrow-icon {
    width: 1.5rem;
    height: 1.5rem;
  }

  @include t-media-query($s-small-up) {
    &.is-visible {

      .c-recommended-products-carousel__outer {
        min-height: 45.9375rem;
      }

      &__product-thumbnail {
        padding: 0;
      }
    }

    @include t-media-query($s-medium-up) {
      &__outer {
        padding: 0 $s-gutter--edge-large;
      }

      &__inner {
        padding-top: 6.875rem;
        padding-bottom: 7.0625rem;
        text-align: center;
        overflow: hidden;
        max-width: 55.1875rem;
        margin: 0 auto;
      }

      &__nav {
        @include t-absolute-center-x-y();
        max-width: 61.625rem;
        padding: 0;
      }
    }

    @include t-media-query($s-large-up) {
      &.is-visible {

        .c-recommended-products-carousel__outer {
          min-height: 45.625rem;
        }
      }
    }

    @include t-media-query($s-xlarge-up) {
      &.is-visible {
        .c-recommended-products-carousel__outer {
          min-height: 48rem;
        }
      }
    }
  }
}