/*
 *  Product Thumbnail
 *    Styles for the product thumbnail object
 *
 *  Version:
 *    1.0.0 - 2021/02/04
 */

.o-product-thumbnail {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  &.has-hover-image:hover{
    .o-product-thumbnail__image{
      opacity: 0;
    }

    .o-product-thumbnail__hover-image{
      opacity: 1;
    }
  }

  //  Image
  &__image-container {
    display: block;
    margin-bottom: 0.4375rem;
  }

  // Title
  &__title {
    margin-bottom: 0;
    text-align: center;

    &-container {
      display: block;
      margin-bottom: 0.375rem;
    }
  }

  &__image-wrapper {
    position: relative;
  }

  &__image{
    opacity: 1;
    transition: opacity .4s;
  }

  &__hover-image{
    opacity: 0;
    transition: opacity .4s;
  }

  &__badge {
    display: block;
    text-align: center;
    color: $s-color--font-focus;
    margin: 0.5rem 0;
  }

  // Price
  &__pricing {
    margin-bottom: 0;
    color: $s-color--font-focus;

    &-compare {
      color: $s-color--font-subdued;
      opacity: 0.5;
      text-decoration: line-through;
    }

    &-container {
      display: block;
      margin-bottom: 1rem;
      text-align: center;
    }
  }

  &__pricing-compare {
    display: inline-block;
    color: $s-color--font-50;
    text-decoration: line-through;
    margin-right: .25rem;
  }

  // Button
  &__button-wrapper {
    text-align: center;

    &.is-desktop {
      display: none;
    }
  }

  &__button{
    .is-thumbnail-cream &{
      color: $s-color--font-focus;
    }

    .is-thumbnail-white &{
      color: $s-color--font-focus;
    }
  }

  /*** Media Queries ***/

  @include t-media-query($s-medium-up) {
    position: relative;
    display: block;

    &__button-wrapper {
      position: absolute;
      right: 0;
      bottom: 0;

      &.is-desktop {
        display: block;
      }

      &.is-mobile {
        display: none;
      }
    }

    &__button-label {
      max-width: 4.6875rem;
    }
  }

  @include t-media-query($s-xlarge-up) {
    &__image-wrapper {
      margin-bottom: 1rem;
    }

    &__title-container {
      margin-bottom: 0.875rem;
    }

    &__button-label {
      max-width: 6.125rem;
    }
  }
}
